.popup {
    &__container {
        position: fixed;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $dark;
    }

    &__additional {
        margin: 20px 0 0 0;
        padding-top: 20px;
        border-top: 1px solid $grey;
    }

    &__link {
        display: block;
        padding: 10px 15px;
        width: 100%;
        border-radius: 4px;
        color: $dark;
        font-weight: 700;
        font-size: 13px;
        text-align: center;
        border: 1px solid $grey;
        text-decoration: none;
        background-color: $color-background;
        transition: all 0.2s ease;
        outline: none;
        cursor: pointer;

        &:hover {
            background-color: rgba($dark, 0.05);
        }
    }
}